import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Picture from '../../../../utils/commonStyles/pictureArea'
import * as util from '../../../../utils/time';

class PictureArea extends Component {
  render () {
    const {info} = this.props
    const summary = info?[
      { title: '(PV)', value: info.pv || 0, help: 'Page View(PV)1' },
      { title: '(UV)', value: info.uv || 0, help: '(Cookie)1' },
      { title: 'IP', value: info.ip_num || 0, help: 'ip' },
      { title: '', value: (util.percent(info.bounce_rate)) || 0, help: '' },
      { title: '', value: (util.sec2time(info.ave_interview_duration)) || 0, help: '' }
    ]:[]
    return <Picture summary={summary} />
  }
}

export default withRouter(connect(({ page }) => ({
  page
}))(PictureArea))
