import colors from '../../../../../utils/charts/color'

function produceOption (data, label) {
  // 
  // legendname
  let legendName = []
  // x
  let xLabel = []
  // series 
  let series = []
  data.map(item=>{
    xLabel.push(item.label)
  })
  label.map(item=>{
    legendName.push(
      item=='pv'?'(PV)':
      item=='uv'?'(UV)':
      item=='ip_num'?'IP':
      item=='bounce_rate'?'':
      item=='ave_interview_duration'?'':""
    )
    series.push({
      name:  item=='pv'?'(PV)':
      item=='uv'?'(UV)':
      item=='ip_num'?'IP':
      item=='bounce_rate'?'':
      item=='ave_interview_duration'?'':"",
      type: 'line',
      data: (function(){
        let res =[]
        data.map(child=>{
          res.push(child[item])
        })
        return res
      })()
    })
  })
  


  const option = {
    color: colors,
    legend: {
      icon: 'circle',
      // data: data.lineLabelName,
      data: legendName,
      textStyle: {
        color: '#647897'
      },
      bottom: 0,
    },
    tooltip: {
      trigger: 'axis'
    },
    grid: {
      height: '70%',
      width: '97%',
      left: '3%',
      top: '20px'
    },
    xAxis: [
      {
        type: 'category',
        axisLabel: {
          color: '#647897'
        },
        axisLine: false,
        data: xLabel
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          color: '#647897'
        },
        axisLine: false,
        splitLine: {
          show: true,
          lineStyle: {
            color: ['#647897'],
            width: 1,
            type: 'solid'
          }
        }
      }
    ],
    series: series
  }

  return option
}

export default produceOption
