import React, { Component } from 'react'
// import moment from 'moment'
import Picture from '../../../../utils/commonStyles/pictureArea'

// const Column = Table.Column

class PictureArea extends Component {
  render () {
    const {info} = this.props
    const summary = info?[
      { title: '', value: info.channel_count || 0, help: '' },
      { title: '', value: info.articleCount || 0, help: '' },
      { title: '(PV)', value: info.pvTotal || 0, help: 'Page View(PV)1' },
      { title: '(UV)', value: info.uvTotal || 0, help: '(Cookie)1' },
      { title: 'IP', value: info.ipTotal || 0, help: 'ip' }
    ]:[]
    return <Picture summary={summary} />
    
  }
}

export default PictureArea
