import React, { Component } from 'react'
// import moment from 'moment'
import Picture from '../../../../utils/commonStyles/pictureArea'

// const Column = Table.Column

class PictureArea extends Component {
  render () {
    const {info}=this.props
    const summary = info?[
      { title: '', value: info.total ||0, help: 'CMS' },
      { title: '', value: info.article_nums ||0, help: '' },
      { title: '', value: info.history_article_nums ||0, help: '' },
      { title: '(PV)', value: info.pv ||0, help: 'Page View(PV)1' },
      { title: '', value: info.comment_nums ||0, help: '' }
    ]:[]
    return <Picture summary={summary} />
  }
}

export default PictureArea
