/* eslint-disable compat/compat */
import axios from 'axios'
import NProgress from 'nprogress'
import { notification, message } from 'antd'
// import { routerRedux } from 'dva/router';
// import { dispatch } from '../store';

/**
 * 
 * http
 * 
 * 
 * |--  NProgress 
 */

// 
axios.defaults.timeout = 5000
axios.defaults.baseURL = '/api/v1'
// axios.defaults.withCredentials = true;

// 
const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: ''
}

// 
axios.interceptors.request.use((config) => {
  // 
  let token = window.localStorage.getItem('token')
  if (token) { // tokenhttp headertoken
    config.headers.Authorization = token
  }
  NProgress.start()
  return config
}, error => Promise.reject(error))

// 
axios.interceptors.response.use((response) => {
  // 
  NProgress.done()
  return response
}, (error) => {
  // 
  // 
  NProgress.done()
  return Promise.reject(error)
})

export default function request (url, options) {
  const newOptions = {
    url,
    method: 'get',
    ...options
  }
  // get 
  if (newOptions.method.toUpperCase() === 'GET' && options.data) {
    newOptions.url = url
    let data = options.data
    let parameter = ''
    for (let key in data) {
      if (data[key]) {
        let integration = (key + '=' + data[key])
        parameter = parameter ? (parameter + '&' + integration) : ('?' + integration)
      }
    }
    newOptions.url += parameter
  }

  if (
    newOptions.method.toUpperCase() === 'POST' ||
    newOptions.method.toUpperCase() === 'PUT' ||
    newOptions.method.toUpperCase() === 'DELETE'
  ) {
    if (!(newOptions.body instanceof FormData)) {
      newOptions.headers = {
        Accept: 'application/json',
        'Content-Type': 'application/json; charset=utf-8',
        ...newOptions.headers
      }
      newOptions.body = JSON.stringify(newOptions.body)
    } else {
      // newOptions.body is FormData
      newOptions.headers = {
        Accept: 'application/json',
        ...newOptions.headers
      }
    }
  }

  return axios(newOptions)
    .then((response) => {
      const { data } = response
      
      if (data && !data.success) {
        message.error(data.msg)
        return false
        // notification.error({
        //   message: data.msg
        // })
      }
      return { ...response.data }
    })
    .catch((error) => {
      console.log('error', error)
      if (!error.response) {
        return false
      }
      // 
      const { status } = error.response
      const errortext = codeMessage[status] || error.response.statusText
      console.log('status', status, typeof (status))
      message.error(` ${status + '  ' + errortext}`)
      // notification.error({
      //   message: ` ${status}`,
      //   description: errortext
      // })

      // 2xx
      // const { dispatch } = store;

      if (status === 401) {
        window.localStorage.removeItem('token')
        window.localStorage.removeItem('username')
        window.location.href = '/#/login'
        // return Promise.reject(error)
        // dispatch(routerRedux.push('/user/login'));
      } else if (status === 403) {
        // dispatch(routerRedux.push('/exception/403'));
      } else if (status <= 504 && status >= 500) {
        // dispatch(routerRedux.push('/exception/500'));
      } else if (status >= 404 && status < 422) {
        // dispatch(routerRedux.push('/exception/404'));
      }

      // 
      // console.log(`${newOptions.method} ${newOptions.url}%o`, error.response);
      return Promise.reject({'error':  '401'})
      // return { error: true, code: status, message: errortext }
    })
}
