import XLSX from 'xlsx';



export function relTime(arr){
    return arr[0].format('YYYY-MM-DD')+','+arr[1].format('YYYY-MM-DD')
};

export function setList(list){
    list.forEach(i=>{
        i.title =i.name
        i.key =i.id
        i.value =i.id
        if(i.children){
            setList(i.children)
        }
    })
    return list
}

export function down(json, document, type){
    var tmpDown
    var tmpdata = json[0];
    json.unshift({});
    var keyMap = []; //keys
    //keyMap =Object.keys(json[0]);
    for (var k in tmpdata) {
        keyMap.push(k);
        json[0][k] = k;
    }
  var tmpdata = [];//json 
        json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
            v: v[k],
            position: (j > 25 ? getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1)
        }))).reduce((prev, next) => prev.concat(next)).forEach((v, i) => tmpdata[v.position] = {
            v: v.v
        });
        var outputPos = Object.keys(tmpdata); //,A1D10
        var tmpWB = {
            SheetNames: ['mySheet'], //
            Sheets: {
                'mySheet': Object.assign({},
                    tmpdata, //
                    {
                        '!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] //
                    })
            }
        };
        tmpDown = new Blob([s2ab(XLSX.write(tmpWB, 
            {bookType: (type == undefined ? 'xlsx':type),bookSST: false, type: 'binary'}//
            ))], {
            type: ""
        }); //
    var href = URL.createObjectURL(tmpDown); //
    document.getElementById("hf").href = href; //a
    document.getElementById("hf").click(); //
    setTimeout(function() { //
        URL.revokeObjectURL(tmpDown); //URL.revokeObjectURL()object URL
    }, 100);
}
function s2ab(s) { //
    var buf = new ArrayBuffer(s.length);
    var view = new Uint8Array(buf);
    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
}
 // 26[0-25] -> [A-Z]
function getCharCol(n) {
    let temCol = '',
    s = '',
    m = 0
    while (n > 0) {
        m = n % 26 + 1
        s = String.fromCharCode(m + 64) + s
        n = (n - m) / 26
    }
    return s
}
